@font-face {
  font-family: 'Lato';
  src: url('/assets/fonts/Lato-Regular.ttf');
}

body {
	background-color: #202020;
	color: #FFF;
	font-family: 'Lato', sans-serif;
	font-size: large;
}

nav {
	width: 100%;
	float: left;
	margin-top: 3%;
}

a {
	color: #FFF;
}

nav a {
	width: 25%; /* 100 divided by how many pages there are */
	float: left;
	font-size: x-large;
	text-align: center;
}

nav a:hover {
	text-decoration: overline;
}

.main-100 {
	width: 100%;
	float: left;
	text-align: center;
	margin-top: 5%;
}

.main-100-textOnly {
	@extend .main-100;
	margin-top: 2% !important;

	p {
		text-align: center;
	}
}

.main-90 {
	width: 90%;
	float: left;
	text-align: center;
	margin-top: 1%;
}

.content-70 {
	width: 70%;
	float: left;

	p {
		text-align: left;
		padding: 0% 3% 0% 3%;
	}
}

.content-60 {
	width: 60%;
	float: left;
}

.content-40 {
	width: 40%;
	float: left;
}

.content-30 {
	width: 30%;
	float: left;
	margin-top: 10%;
}

/* tablet */
@media screen and (max-width: 1000px) {
	.main-90 {
		width: 100%;
	}

	.content-70 {
		width: 100%;
	}

	.content-30 {
		width: 20%;
		padding-left: 40%;
		margin-top: 0%;
	}
}

/* phone */
@media screen and (max-width: 767px) {
	.main-90 {
		width: 100%;
	}

	.content-70 {
		width: 100%;
	}

	.content-30 {
		width: 40%;
		padding-left: 30%;
		margin-top: 0%;
	}

}